function msg(id: string, arg: any) {
    window.onTrailPassionCoreMessage(id, arg);
}

const MOCKS = {
    getAppName(id: string) {
        msg(id, "Hybrid Guide");
    },

    getInitialConfig(id: string) {
        msg(id, {
            title: "Minervois-Carroux",
            theme: {
                bgP: "#33cbff", bgS: "#ff5c00",
                bg0: "#000", bg3: "#666"
            }
        });
    },

    getPackImage(id: string, packId: string) {
        window.setTimeout(() => {
            msg(id, `https://trail-passion.net/tfw/pub/pack/${packId}/bg.jpg`);
        }, 100 + 300 * Math.random());
    },

    getPacks(id: string, forceRefresh = false) {
        window.setTimeout(() => msg(id, [
            {
                id: 45, title: { fr: "Audio guide" },
                shortDescription: { fr: "Des circuits audio-guidés pour tout les curieux qui veulent en savoir plus sur l'histoire de nos villages." }
            },
            {
                id: 43, title: { fr: "Randonnées à la journée" },
                shortDescription: { fr: "Randonnée à la journée pour une découverte de notre territoire." }
            },
            {
                id: 56, title: { fr: "Services" },
                shortDescription: { fr: "Randonnée à la journée pour une découverte de notre territoire." }
            }
        ]), 500 + 1000 * Math.random());
    }
}


export default MOCKS;
