/**
 * The full state of the application is stored and managed here.
 */
import { createStore } from 'redux'
import { IAppState, IAction, IPack } from "../types"
import castBoolean from "../tfw/converter/boolean"

const INITIAL_STATE: IAppState = {
    packs: [],
    setRefreshingPacks: false
};

function reducer(state: IAppState | undefined = INITIAL_STATE, action: IAction): IAppState {
    try {
        switch (action.type) {
            case "set-packs": return update(setPacks, state, action);
            case "set-pack-background": return update(setPackImage, state, action);
            case "set-refreshing-packs": return update(setRefreshingPacks, state, action);
        }
        return state;
    }
    catch (ex) {
        console.error(ex);
    }
}

/**
 * Helper to make sure the returned state is not a mutation of the initial one.
 */
function update(slot: (state: IAppState | undefined, action: IAction) => {},
    state: IAppState | undefined,
    action: IAction) {
    return Object.assign({}, state, slot(state, action));
}

function setPacks(state: IAppState | undefined = INITIAL_STATE, action: IAction): {} {
    return { packs: action.packs, refreshingPacks: false };
}

function setPackImage(state: IAppState | undefined = INITIAL_STATE, action: IAction): {} {
    return {
        packs: state.packs.map(pack => {
            if (pack.id !== action.packId) return Object.assign({}, pack);
            return Object.assign({}, pack, { background: action.url });
        })
    };
}

function setRefreshingPacks(state: IAppState | undefined = INITIAL_STATE, action: IAction): {} {
    return { refreshingPacks: action.value };
}

const store = createStore(reducer);
export default {
    store, dispatch: store.dispatch,
    setPacks(packs: IPack[]) { return { type: "set-packs", packs }; },
    setPackImage(packId: string, url: string) { return { type: "set-pack-background", packId, url }; },
    setRefreshingPacks() { return { type: "set-refreshing-packs", value: true }; }
};
