import Theme from "../tfw/theme"

export default { apply, register }

Theme.register("default", {
    bgP: "#2ca600",
    bgS: "#FFA000",
    bg0: "#C8C4C0",
    bg3: "#FFF"
});

function apply(name: string = "default") {
    Theme.apply(name);
}

function register(name: string, theme: {}) {
    Theme.register(name, theme);
}
