/**
 * This application aims to be launched on mobile devices running Androïd or iOS.
 * Such operating system will povide a global object called `TrailPassionCore`.
 * It is the only way out of our WebApp: we never access the web directly.
 *
 * In order to manage asynchronous calls to the mobile, we provide a single
 * callback: `window.onTrailPassionCoreMessage( id, message )`.
 */

import Api from "./core-api"

export default {
    getAppName :wrap("getAppName"),
    getInitialConfig: wrap("getInitialConfig"),
    getPacks: wrap("getPacks"),
    getPackImage: wrap("getPackImage")
}

type TCallback = ({ }) => void;

let ID: number = 0;
const CALLBACKS: { [key: string]: { onMessage: TCallback, onError: TCallback } } = {};

function register(id: string, onMessage: TCallback, onError: TCallback) {
    CALLBACKS[id] = { onMessage, onError };
}

function unregister(id: string) {
    delete CALLBACKS[id];
}

function wrap(funcName: string) {
    const api: (...args: any[]) => void = Api[funcName];

    return function(...args: any[]) {
        return new Promise((resolve, reject) => {
            const id = `_${ID++}_`;
            const unreg = () => unregister(id);
            const onMessage = (msg: any) => {
                console.log(`Message from ${funcName}:`, msg);
                unreg();
                resolve(msg);
            }
            const onError = (err: any) => {
                console.log(`Error from ${funcName}:`, err);
                unreg();
                reject(err);
            }
            register(id, onMessage, onError);
            try {
                api(id, ...args);
            } catch (ex) {
                console.error(`Exception in ${funcName}:`, ex);
                onError(ex);
            }
        });
    };
}

window.onTrailPassionCoreMessage = function(id: string, msg: any) {
    const { onMessage } = CALLBACKS[id];
    if (typeof onMessage !== 'function') {
        throw Error(`There is no valid message callback with id="${id}"!`);
    }
    window.setTimeout(() => onMessage(msg));
}

window.onTrailPassionCoreError = function(id: string, err: any) {
    const { onError } = CALLBACKS[id];
    if (typeof onError !== 'function') {
        throw Error(`There is no valid error callback with id="${id}"!`);
    }
    window.setTimeout(() => onError(err));
}
