import MOCKS from "./core-mocks"

/* exported window.TrailPassionCore */
const Api = {
    getAppName: mock("getAppName", (id: string) => window.TrailPassionCore.getAppName(id)),
    getInitialConfig: mock("getInitialConfig", (id: string) => window.TrailPassionCore.getInitialConfig(id)),
    /**
     */
    getPackImage: mock("getPackImage", (id: string, packId: string) => window.TrailPassionCore.getPackImage(id, packId)),
    /**
     * [msg description]
     *
     * @param   id  [description]
     * @param   arg [description]
     * @returns     [description]
     */
    getPacks: mock("getPacks", (id: string) => window.TrailPassionCore.getPacks(id))
};

export default Api;


function mock(name: string, lambda: () => void): () => void {
    if (window.TrailPassionCore && window.TrailPassionCore[name]) return lambda;
    return MOCKS[name];
}

console.info("window.TrailPassionCore=", window.TrailPassionCore);
console.info("Api=", Api);
