import React from "react";
import ReactDOM from 'react-dom'
import { Provider } from 'react-redux'
import App from "./App";
import Theme from "./tp/theme"
import Core from "./guide/core"
import Splash from "./tp/splash"
import State from "./state"
import { IPack } from "./types"
import "./tfw/font/josefin.css"

import "./index.css"
import Button from "./tfw/view/button"

Theme.apply("default");

async function debug() {
    State.dispatch(State.setRefreshingPacks());

    const appName = await Core.getAppName();
    console.info("appName=", appName);

    const packs = await Core.getPacks();
    State.dispatch(State.setPacks(packs));
}

async function start() {
    const config = await Core.getInitialConfig();

    console.info("config=", config);
    Theme.register("default", config.theme);
    Theme.apply("default");
    ReactDOM.render(<Provider store={State.store}>
        <App />
        <Button icon="bug" warning={true} small={false} classes="debug"
            onClick={debug} />
    </Provider>, document.getElementById("root"));
    const packs = await Core.getPacks();
    State.dispatch(State.setPacks(packs));
    Splash.hide();
    for (const pack of packs) {
        if (typeof pack.background === "string") continue;
        const url = await Core.getPackImage(pack.id);
        State.dispatch({ type: "set-pack-background", packId: pack.id, url });
    };
}


start();
