import React from "react"
import { connect } from 'react-redux'
import Flex from "./tfw/layout/flex"
import Stack from "./tfw/layout/stack"
import Scroller from "./tfw/view/scroller"

import Pack from "./guide/view/pack"
import State from "./state"
import { IAppState } from "./types"

import "./tfw/font/josefin.css"
import "./App.css";


class App extends React.Component<IAppState, {}> {
    componentDidMount() {
    }

    componentDidUpdate() {
    }

    render() {
        return (<Stack value="main">
            <Scroller key="main"
                classes="thm-bg0"
                refreshable={true}
            >{this.props.packs.map(pack => (
                <Pack
                    key={pack.id}
                    id={pack.id}
                    title={pack.title}
                    background={pack.background}
                    shortDescription={pack.shortDescription} />))
                }</Scroller>
        </Stack>)
    }
}


function mapStateToProps(state: IAppState) {
    const newState = {
        packs: state.packs.slice(),
        refreshingPacks: state.refreshingPacks
    };
    return newState;
}

function mapDispatchToProps(dispatch) {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
