import React from "react"
import { IPack } from "../../types"
import Card from "./card"
import Image from "../../tfw/view/image"
import "./pack.css"

interface IPackProps extends IPack {
}

function translate(text: any) {
    if (typeof text === 'string') return text;
    if (typeof text.fr === 'string') return text.fr;
    return "...";
}

export default class Pack extends React.Component<IPackProps, {}> {
    render() {
        const p = this.props;
        return (<Card>
            <div className="guide-view-pack">
                <Image classes="background" src={p.background} />
                <div className="title thm-bg2-top">{translate(p.title)}</div>
                <div className="desc">{translate(p.shortDescription)}</div>
            </div>
        </Card>);
    }
}
