import React from "react"
import "./card.css"
import Touchable from "../../tfw/view/touchable"
import Theme from "../../tfw/theme"

export default class Card extends React.Component<{}, {}> {
    ref = React.createRef();

    render() {
        return (<div className="guide-view-card thm-ele-button thm-bg2">
            <Touchable color={Theme.bgSL()}>{this.props.children}</Touchable>
        </div>);
    }
}
